.App-header {
  background-color: #282c34;
  min-height: 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-weight: bold;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.tab { white-space: pre; }

.page-container {
  display: flex;
  flex: 1;
  flex-direction: column;
  min-height: 1500px;
}

.section-container {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  /* font-family: 'Do Hyeon', sans-serif; */
}

.dohyeon-font-text {
  font-family: 'Do Hyeon', sans-serif;
}

.jua-font-text {
  font-family: 'Jua', sans-serif;
}
